.hero {
  padding-top: 160px;
  min-height: 800px;
  background-color: var(--dark-color);

  @include high-desktop {
    min-height: 100%;
  }

  @include mobile {
    padding-top: 120px;
  }

  &__container {
    position: relative;
    @include flex-v-center;

    @include high-tablet {
      max-width: 520px;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__content {
    max-width: 516px;

    @include high-desktop {
      max-width: 700px;
    }

    @include high-tablet {
      text-align: center;
    }
  }

  &__image {
    transform: translateX(167px);

    img {
      vertical-align: bottom;
    }

    @include high-desktop {
      background-position: bottom center;
      transform: translateX(50px);
    }

    @include high-tablet {
      transform: translate(0%, -30px);
    }

  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    color: var(--light-color);
    text-transform: uppercase;

    @include high-desktop {
      font-size: 57px;
    }
    @include mobile {
      font-size: 35px;
    }

    &-text {
      max-width: 90%;
      display: block;
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;
      text-transform: initial;

      @include high-tablet {
        margin: 0 auto;
      }
      @include mobile {
        font-size: 16px;
        line-height: 120%;
      }
    }
  }

  &__link {

    @include high-tablet {
      position: absolute;
      width: 90%;
      left: 50%;
      padding: 25px 54px;
      transform: translateX(-50%);
      z-index: 9;
      bottom: 30px;
    }
  }
}

