.benefits {
  background-color: #f9f9f9;


  &__container {
    @include high-desktop {
      max-width: 900px;
    }

    @include high-tablet {
      max-width: 520px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    @include high-desktop {
      grid-template-columns: repeat(4, 1fr);
    }

    @include high-tablet {
      text-align: center;
    }
  }

  &__item {
    grid-column: 4 span;

    @include high-desktop {
      &:not(:last-child) {
        margin-bottom: 60px;
      }
    }
  }

  &__subtitle {
    display: inline-flex;
    align-items: center;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 140%;
    color: var(--dark-color);

    @include high-desktop {
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--text-color);

    @include mobile {
      font-size: 16px;
    }
  }
}
