.graph-modal {
  &::before {
    @include tablet {
      display: none;
    }
  }
}

.popup {
  padding: 40px;
  width: 100%;
  max-width: 940px;

  img {
    border-radius: 10px;
  }

  &__top {
    display: flex;
    gap: 40px;
    margin-bottom: 40px;

    @include mobile {
      gap: 20px;
    }
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark-color);

    @include mobile {
      line-height: 100%;
      font-size: 20px;
    }
  }

  &__post {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: var(--text-color);

    @include mobile {
      font-size: 16px;
    }
  }

  &__subtitle {
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    color: var(--dark-color);
    margin-bottom: 20px;
  }

  &__bottom-content {
    padding-top: 20px;
    padding-right: 20px;
    max-height: 375px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--dark-color);
    margin-bottom: 5px;

    @include mobile {
      font-size: 16px;
    }
  }

  .simplebar-track.simplebar-vertical {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 5px;
    height: 345px;
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
  }

  .simplebar-scrollbar::before {
   left: 0;
   right: 0;
   opacity: 1;
   width: 5px;
   border-radius: 40px;
   background-color: var(--dark-color);
  }

  @include small-tablet {
    padding-top: 80px;
  }

  @include mobile {
    padding: 20px;
    padding-top: 40px;
  }
}

.tabs {
  &__menu {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 70%;

    &-item {
      @include mobile {
        margin-right: 40px;
        margin-bottom: 0;
      }
    }

    &-btn {
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;
      color: var(--dark-color);

      &:focus {
        background-color: transparent;
        color: var(--accent-color);
      }

      &--active {
        outline: none;
        background-color: transparent;
        color: var(--accent-color);
      }
    }
    @include mobile {
      flex-wrap: wrap;
    }
  }
}
