.nav {
  &__list {
    @include flex-v-center;
    gap: 40px;

    @include high-tablet {
      @include flex-all-center;

      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: var(--light-color);

    @include high-tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
      color: var(--light-color);
    }
  }

  @include high-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 99;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--dark-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
