.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 25px 54px;
  border-radius: 5px;
  background-color: var(--accent-color);

  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  text-align: center;
  color: var(--light-color);

  &--dark {
    background-color: var(--dark-color);
  }
}
