.social {
  display: flex;
  align-items: center;
  gap: 10px;

  &__link {
    display: block;
    width: 30px;
    height: 30px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--dark-color);
      object-fit: cover;
      transition: transform .3s ease-in-out;

      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
