.about {
  background-color: var(--light-color);

  &__container {
    @include high-desktop {
      max-width: 900px;
    }

    @include high-tablet {
      max-width: 520px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 60px;
    max-width: 892px;
    font-weight: 400;
    font-size: 30px;
    line-height: 140%;
    color: var(--dark-color);

    &-highlight {
      font-weight: 600;
    }

    @include high-tablet {
      text-align: center;
      line-height: 120%;
      margin-bottom: 40px;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include high-tablet {
      display: flex;
      gap: 35px;
      flex-direction: column;
      text-align: center;
      // align-items: center;
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 6 span;

    }
  }

  &__digit {
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    color: var(--accent-color);

    @include high-tablet {
      line-height: 100%;
    }

    @include mobile {
      font-size: 60px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--text-color);
  }

  @include mobile {
    font-size: 16px;
    line-height: 120%;
  }
}
