.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 30px 0;
  width: 100%;

  &__logo {
    @include mobile {
      width: 136px;
    }
  }

  &__container {
    @include flex-all-sb;

    @include high-tablet {
      align-items: center;
      justify-content: initial;
    }
  }

  &__phone {
    @include high-tablet {
      position: relative;
      margin-left: auto;
      margin-right: 10px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      background: rgba(255, 255, 255, 0.08);
      width: 44px;
      height: 44px;
      font-size: 0;
      background-image: url("../img/svg/phone.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 24px 24px;
    }
  }
}
