.burger {
  display: none;
  position: relative;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.08);
  width: 44px;
  height: 44px;

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100px;
    height: 2px;
    width: 26px;
    background-color: var(--light-color);
    transform: translate(-50%, -50%);

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      left: 8px;
      width: 13px;
      margin-top: 7px;
      transform: translate(0, -50%);
    }
  }

  @include high-tablet {
    display: block;
  }
}

.burger-close {
  display: none;
  position: absolute;
  right: var(--container-offset);
  top: 30px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.08);
  width: 44px;
  height: 44px;
  background-image: url("../img/svg/close.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 14px 14px;

  @include high-tablet {
    display: block;
  }
}
