.tariffs {
  padding-top: 0;

  &__container {
    @include high-desktop {
      max-width: 900px;
    }

    @include high-tablet {
      max-width: 520px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include high-tablet {
      display: flex;
      flex-direction: column;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 4 span;
    border: 1px solid rgba(20, 16, 36, 0.2);
    border-radius: 20px;
    padding: 40px;
    transition: all .5s ease-in-out;

    &:hover {
      background-color: var(--dark-color);
      border: 1px solid var(--dark-color);
      transform: scale(1.02);

      .tariffs__subtitle,
      .tariffs__price,
      .tariffs__descr {
        color: var(--light-color);
      }

      // .tariffs__price {
      //   color: var(--light-color);
      // }
      // .tariffs__descr {
      //   color: var(--light-color);
      // }
    }

    @include high-desktop {
      padding: 25px;
    }
  }

  &__subtitle {
    margin: 0;
    display: flex;
    align-items: center;
    margin-bottom: 60px;
    font-weight: 600;
    font-size: 30px;
    line-height: 140%;
    text-align: center;
    color: var(--dark-color);

    &::before {
      content: '';
      display: block;
      margin-right: 5px;

      width: 20px;
      height: 3px;
      background-color: currentColor;
    }

    &::after {
      content: '';
      display: block;
      margin-left: 5px;

      width: 20px;
      height: 3px;
      background-color: currentColor;
    }

    @include high-desktop {
      margin-bottom: 35px;
    }

    @include high-tablet {
      margin-bottom: 5px;
    }
  }

  &__price {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 50px;
    line-height: 140%;
    text-align: center;
    color: var(--dark-color);

    @include high-desktop {
      font-size: 35px;
    }

    @include high-tablet {
      margin-bottom: 0;
    }
  }

  &__descr {
    margin-bottom: auto;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    text-align: center;
    color: var(--text-color);
  }

  &__btn {
    margin-top: 61px;
    min-width: 100%;
    transition: all .3s ease-in-out;

    @include high-desktop {
      margin-top: 40px;
      padding: 25px 25px;
    }
    @include high-tablet {
      margin-top: 20px;
      padding: 25px 25px;
    }

    // &::after {
    //   content: "";
    //   position: absolute;
    //   inset: 0;
    // }
  }
}
