.order {
  @include image-set("../img/percent");

  padding: 87px 0;
  background-color: var(--accent-color);
  background-size: cover;
  background-position: bottom center;

  @include mobile {
    padding: 40px 0;
  }
  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    @include high-desktop {
      max-width: 900px;
      grid-template-columns: repeat(2, 1fr);
    }
    @include high-tablet {
      max-width: 520px;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  }

  &__left {
    padding-left: 60px;
    grid-column: 5 span;

    @include high-desktop {
      padding-left: 0;
      grid-column: unset;
    }
    @include high-tablet {
      text-align: center;
    }
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    grid-column: 7 span;
    padding-right: 60px;

    @include high-desktop {
      padding-right: 0;
      grid-column: unset;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 120%;
    color: var(--light-color);

    @include mobile {
      font-size: 30px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--light-color);
  }

  &__form {
    max-width: 500px;
  }
}
