.section-title {
  margin: 0;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 50px;
  line-height: 140%;
  color: var(--dark-color);

  @include high-tablet {
    font-size: 35px;
    text-align: center;
  }
  @include mobile {
    font-size: 30px;
    margin-bottom: 40px;
    line-height: 100%;
}
}
