.trainers {
  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));

    @include high-desktop {
      padding-left: max(15px, calc((100% - var(--content-width-high-desktop)) / 2));

    }
  }

  &__content {
    margin-bottom: 75px;
  }

  .swiper-slide {
    width: 360px;

    @include mobile {
      width: 220px;
    }
  }

  .swiper-scrollbar-drag {
    background-color: var(--dark-color);
  }

  &__image {
    border-radius: 20px;
    max-height: 500px;
    margin-bottom: 20px;
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 140%;
    color: var(--dark-color);
  }

  &__post {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--text-color);
    margin-bottom: 20px;

    @include high-desktop {
      font-size: 16px;
    }
  }


  &__btn {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--accent-color);
  }

  &__nav {
    @include flex-all-sb;
  }

  &__scroll {
    background: rgba(20, 16, 36, 0.08);
    border-radius: 40px;
    width: 360px;
    height: 5px;

    @include mobile {
      width: 100%;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 10px;

    @include mobile {
      display: none;
    }
  }

  &__slider-btn {
    @include bg-cover;


    width: 40px;
    height: 40px;

    &--prev {
      background-image: url("../img/prev.svg");
    }

    &--next {
      background-image: url("../img/next.svg");
    }
  }

  @include high-desktop {
    .container {
      max-width: 900px;
    }
  }
}
